        </header>
        <div class="content">
            <!-- <div class="videoitem">
                <div class="video">
                    <video controls id="video">
                      <source src="http://vt1.doubanio.com/201912191850/9074885ad934bb7d4e1eddeb6756f6db/view/movie/M/302560730.mp4" type="video/mp4">不支持
                    </video>
                </div>
                <div class="videoinfo clearfix">
                    <div class="fl">《张艺兴》</div>
                    <div class="fr">
                        <div class="wish fl">
                            <i></i>   
                            <span>111111</span>
                        </div>
                        <div class="collect fr">
                            <i></i>
                            <span>222</span>
                        </div>
                    </div>
                </div>
                <div class="movieData clearfix">
                    <div class="fl movieImg">
                        <img class="autoimg" src="https://img1.doubanio.com/view/photo/s_ratio_poster/public/p2577438847.jpg" alt=""> 
                    </div>
                    <div class="fr movieInfo">
                        <div class="movieTitle one-txt">张艺兴</div>
                        <div class="actor">演员：张艺兴 | 张艺兴</div>
                        <div class="time">上映日期：</div>
                    </div>
                </div>
            </div> -->

            <div class="videoitem" v-for="(item,index) in tearsData" :key="index" >
                <div class="video">
                    <video controls id="video" class="video">
                      <source :src="tearsData[index].trailer_urls[0]" type="video/mp4">不支持
                    </video>
                </div>
                <div class="movieData clearfix">
                    <div class="fl movieImg">
                        <img class="autoimg" :src="tearsData[index].images.small" alt=""> 
                    </div>
                    <div class="fr movieInfo">
                        <div class="movieTitle one-txt">{{tearsData[index].title}}</div>
                        <div class="actor one-txt">演员：{{tearsData[index].casts[0].name}} | {{tearsData[index].casts[1].name}} | {{tearsData[index].casts[2].name}}</div>
                        <div class="time">上映日期：{{tearsData[index].pubdate}}</div>
                    </div>
                </div>
                <div class="videoinfo clearfix">
                    <div class="fl">《{{tearsData[index].trailers[0].title}}》</div>
                    <div class="fr">
                        <div class="wish fl">
                            <i></i>   
                            <span>{{tearsData[index].wish_count}}</span>
                        </div>
                        <div class="collect fr">
                            <i></i>
                            <span>{{tearsData[index].collect_count}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { createNamespacedHelpers } from "vuex";
    const { mapState } = createNamespacedHelpers("teaserModule");

    export default {
        name:'teaser',

        computed:{
            ...mapState(['comingMovieData','detailDataId','tearsData'])
        },

        created() {
            this.$toast.loading({
              duration: 0,
              message: '加载中...'
            });
            
            this.axios({
                method: 'GET',
                url: 'https://douban.uieee.com/v2/movie/coming_soon'
            }).then(result => {
                // console.log('result.data.subjects ==> ', result.data.subjects);
                this.$store.commit('teaserModule/getDetailData',result.data.subjects);

                for(let i = 0 ,len = this.detailDataId.length;i<len; i++){
                    // console.log('this.detailDataId[i]=>',this.detailDataId[i]);
                    this.getTeaserData(this.detailDataId[i]);
                }
                this.$toast.clear();

            }).catch(err => {
                // console.log('err ==> ', err);
                this.$toast.clear();
            })
        },

        methods:{
            getTeaserData(id){
                //开启加载提示
                this.$toast.loading({
                  duration: 0,
                  message: '加载中...'
                });

                this.axios({
                  method: 'GET',
                    url: 'https://douban.uieee.com/v2/movie/subject/' + id
                }).then(result => {
                    // console.log('TeaserData result => ', result.data);
                    this.$store.commit('teaserModule/getTeaserData',result.data);
                    this.$toast.clear();

                }).catch(err => {
                //   console.log('err=>', err);
                    this.$toast.clear();

                })
            },

            // 返回上一步
            back() {
                this.$router.push({name:'home'});
            },
        },

        mounted(){
            // console.log('this.detailDataId=>',this.detailDataId);

            // console.log('this.detailDataId[0]=>',this.detailDataId[1]);

            // this.getTeaserData(34925491);
            // this.getTeaserData(this.detailDataId[0]);
        }
    }
</script>

<style lang="less" scoped>
    .teaser{
      header{
        width: 100%;
        background-color: #697A9C;
        /deep/.van-nav-bar{
          background-color: transparent;
        }
        .van-nav-bar__text{
          color: #fff;
          font-size: 16px;
        }
        .van-nav-bar__title{
          color: #fff;
          font-size: 18px;
        }
        .van-icon-arrow-left:before{
           color: #fff;
           font-size: 20px;
        }
        /deep/.van-icon{
          padding: 0 6px;
      }
    }

    .content{
        position: fixed;
        top:47px;
        bottom:0;
        left: 0;
        right: 0;
        overflow-y: auto;
        padding: 0 4px;
        .videoitem{
            margin-bottom:20px;
            padding: 0 4px 10px;

            .video{
                width: 100%;
                height: 100%;
                >video{
                    width: 100%;
                    height: 100%;
                    border-radius: 6px;
                }
            }
            .videoinfo {
                font-size: 16px;
                color: #333;
                >div span{
                    font-size: 14px;
                    color: #999;
                    padding: 0 4px;
                }
                >div i{
                    float: left;
                    display:block;
                    width: 24px;
                    height: 24px;
                    }
                    .wish>i{
                        background: url(../../assets/images/like_love.png)no-repeat center center;
                        background-size: 22px 22px;

                        &:hover{
                            background: url(../../assets/images/like_love_active.png)no-repeat center center;
                            background-size: 22px 22px;
                        }
                    }
                    .collect>i{
                        background: url(../../assets/images/collect_star.png)no-repeat center center;
                        background-size: 22px 22px;
                        &:hover{
                            background: url(../../assets/images/collect_star_active.png)no-repeat center center;
                            background-size: 22px 22px;
                        }
                    }
                }
            }
            .movieData{
                padding: 4px 0 4px 10px;
                background-color: #f5f5f5;
                .movieInfo{
                    width: calc(~"100% - 80px");
                    .movieTitle{
                        font-size: 18px;
                        font-weight: bold;
                        padding: 4px 0;
                    }
                    .actor,.time{
                        font-size: 14px;
                        color: #999;
                        padding: 4px 0;
                    }
                }
                .movieImg{
                    width: 65px;
                    border: 1px solid #697A9C;
                }
            }
        }
    }
</style>